import { Component, OnInit } from '@angular/core';
import { Card } from '../card.module';
import { DataService } from '../data.service';
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {
  cards$: Card[];
  cardName = '';
  bool1 = false;

  isavailable = true;

  rand = 1234;
  myval = 0;
  output = [];
  sNumber = '';
  marks = [0, 0];
  fullanswer = '';
  fullAnswers = [];

  constructor(private dataService: DataService) {
    // this.output = this.singledigit(this.rand);
    this.output = this.makeRandnumber();
    this.makeRandnumber();
  }

  ngOnInit() {
    return this.dataService.getCards().subscribe(data => (this.cards$ = data));
  }

  makeRandnumber() {
    const randNumbers = [];
    let singleNumber = 0;
    for (let i = 0; i < 4; ++i) {
      singleNumber = Math.floor(Math.random() * 9);
      if (randNumbers.length === 0) {
        randNumbers.push(singleNumber);
      } else {
        if (randNumbers.indexOf(singleNumber) === -1) {
           randNumbers.push(singleNumber);
        } else {
          --i;
        }
      }
    }
    console.log(randNumbers);
    return randNumbers;
  }

  singledigit(num: number) {
    const finalNumbers = [];
    const stringNumber = num.toString();
    for (let i = 0; i < stringNumber.length; i += 1) {
      finalNumbers.push(+stringNumber.charAt(i));
    }
    console.log(finalNumbers);
    return finalNumbers;
  }

  firstclick() {
    console.log('clicked!');
    this.bool1 ? (this.bool1 = false) : (this.bool1 = true);
  }

  check(num: number) {
    this.marks = [0, 0];
    const choice = this.singledigit(num);
    for (let i = 0; i < choice.length; i += 1) {
      for (let j = 0; j < this.output.length; j += 1) {
        if (choice[i] === this.output[j] && i === j) {// HIT
          this.marks[0] += 1;
        } else if (choice[i] === this.output[j]) {// PASS
          this.marks[1] += 1;
        }
      }
    }
    this.fullanswer = this.marks[0] + 'HITS, ' + this.marks[1] + 'PASS ' + '[' + num + ']';
    this.fullAnswers.push(this.fullanswer);
    console.log(this.marks);
  }
}